 .Links:hover{
  cursor: pointer;
} 
.MainBar{
  /* padding: 50px; */
  border: 1px solid rgb(202, 150, 66);
  padding-top: 30px;
  text-align: left;
  margin-top: 20px;
  vertical-align: top;
  display: inline-block;
   width: 50vw;
   margin-left: 2vw;
  /* margin-right: 50px; */
}
.Home,.Contact,.Project,.Profile,.Skills{
  display:block;
  text-align: center;
  margin-top: 20px;
  width: auto;
  margin-bottom: 0px;
}


.Labels
{
  color: white;
  padding: 8px;
  background-color:#2A4850;
 text-shadow: 1px 1px black;
 border-style: inset;
 border-color: silver;
 margin-left: 0vw;
 margin-top: 10px;
 margin-bottom: 10px;
 transition: ease-in-out;
 border-radius: 20%;
 font-size: 2vw;
 width:12vw
}
.profile {
  border-radius: 30%;
  height: 150px;
  width: 14vw;
}
.RightBar{
  padding-top: 30px;
  border: 1px solid papayawhip;
  display: inline-block;
  margin-left: 2vw;
  text-align: left;
  margin-top: 20px;
  vertical-align: top;
  margin-right: 2vw;
  width: 15vw;
}
.RightLinks{
  text-decoration: none;
  color: white;
  background-color:#2A4850;
  border-style: inset;
 border-radius:20%;
 height: 50px;
 width:10vw;
margin-bottom: 15px;
 list-style-type: none;
}
.RightLinks:hover{
  background-color: #282c34;
  transform: rotateX(360deg);
  transition: transform 2s;
}
.headicons{
  width: 5vw;
  height: 45px;
  border-radius: 50%;
}
.headicons:hover{
  transform: scaleX(-1);
}
.Logos
{
  margin-left: 5px;
  width: 2vw;
  height: 3vw;
  border-radius: 30%;
}
.Logos:hover{
  border-radius:50%;
  margin-left: 5px;
  width: 2vw;
  height: 3vw;
}
.App{
  width: 100vw;
  height: 100%;
  margin-top: 20px;
}
